.work {
	padding-bottom: $gap;
	background-color: $bg-pale-blue;
}

.grid {
	@extend .container;
	display: flex;
	justify-content: space-between;
	gap: 0.5rem;

	@include small {
		display: block;
	}

	&-col {
		flex: 1;
	}

	&-item {
		padding: $gap-lg;
		opacity: 0;
		transition: opacity .3s ease, background-color 0.3s ease;

		@include medium {
			padding: $gap;
		}

		@include small {
			padding: $gap 0;
		}

		&:hover {
			background: $white;
		}

		&.in-view {
			opacity: 1;
		}

		a {

			display: block;

			> * {
				font-weight: 700;
				line-height: 1.2;
				text-transform: uppercase;
				color: $blue;
			}
		}

		img {
			display: block;
			width: 100%;
			border: 1px solid $border-gray;
		}

		h3 {
			font-size: 1rem;
			text-transform: uppercase;
		}

		p {
			font-size: 1rem;
			color: $dark-gray;

			& > a {
				display: inline;
			}
		}
	}

	&-intro {
		p {
			font-weight: 700;
			color: $blue;
		}
	}
}
